<template>
  <div class="lightbox" v-if="image" @click="close">
    <transition :name="direction">
      <lightbox-image :image="image" :key="image"></lightbox-image>
    </transition>
    <div class="lightbox__close" @click="close">
      <i class="far fa-window-close icon__close"></i>
    </div>
    <div class="lightbox__btn lightbox__next" @click.stop.prevent="next">
      <i class="fas fa-chevron-right icon-chevron"></i>
    </div>
    <div class="lightbox__btn lightbox__prev" @click.stop.prevent="prev">
      <i class="fas fa-chevron-left icon-chevron"></i>
    </div>
  </div>
</template>
<script>
import "./LightboxDirective";
import store from "./LightboxStore";
import LightboxImage from "./LightboxImage";

export default {
  components: {
    LightboxImage
  },
  data() {
    return {
      state: store.state,
      direction: "next"
    };
  },
  computed: {
    image() {
      if (this.state.index !== false) {
        return this.state.images[this.state.index];
      }
    },
    transition() {
      return this.direction;
    }
  },
  methods: {
    close() {
      store.close();
    },
    next() {
      store.next();
      this.direction = "next";
    },
    prev() {
      store.prev();
      this.direction = "prev";
    }
  }
};
</script>
<style >
.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(253, 253, 253, 0.856);
}
.lightbox__close {
  position: fixed;
  top: 90px;
  right: 20px;
  width: 50px;
  height: 50px;
}
.lightbox__btn {
  position: fixed;
  top: 55%;
  width: 50px;
  height: 50px;
  margin: -40px 0.5em 0 0.5em;
  transition-duration: 0.4s;
}
.icon-chevron {
  font-size: 5em;
  cursor: pointer;
  color: rgba(7, 139, 255, 0.541);
}
.lightbox__next {
  right: 20px;
}
.lightbox__prev {
  left: 20px;
}
.icon__close {
  font-size: 3em;
  color: rgba(7, 139, 255, 0.541);
  cursor: pointer;
  transition-duration: 0.4s;
}
.icon__close:hover,
.lightbox__btn:hover {
  transform: scale(1.2);
}

.next-enter-active,
.next-leave-active {
  transition: transform 1s;
}
.next-enter {
  transform: translateX(100px);
}
.next-leave {
  transform: translateX(-100px);
}
.prev-enter-active,
.prev-leave-active {
  transition: transform 1s;
}
.prev-enter {
  transform: translateX(-100px);
}
.prev-leave {
  transform: translateX(100px);
}
</style>
