<template>
  <section id="competences">
    <h2 class="mt-4 text-center">
      <i class="fa fa-code code mr-2"></i>
      <span v-if="languageType === 'en'">Skills</span>
      <span v-else-if="languageType === 'fr'">Compétences Techniques</span>
      <span v-else-if="languageType=== 'pt'">Competências Técnicas</span>
    </h2>
    <b-container>
      <b-row>
        <b-col md="6" sm="12">
          <h3 class="mt-4 text-center php">
            PHP
            <i class="fab fa-php"></i>
          </h3>
          <b-progress :value="php" :max="max" show-progress animated></b-progress>

          <h3 class="mt-4 text-center js">
            JS
            <i class="fab fa-js-square"></i>
          </h3>

          <b-progress :value="js" :max="max" show-progress animated variant="warning"></b-progress>
          <h3 class="mt-4 text-center sf">
            SYMFONY
            <i class="fab fa-symfony"></i>
          </h3>
          <b-progress :value="symfony" :max="max" show-progress animated variant="success"></b-progress>
          <h3 class="mt-4 text-center vjs">
            VUEJS
            <i class="fab fa-vuejs"></i>
          </h3>
          <b-progress :value="vjs" :max="max" show-progress animated variant="danger"></b-progress>
          <h3 class="mt-4 text-center bootstrap">
            Bootstrap
            <i class="fab fa-bootstrap"></i>
          </h3>
          <b-progress :value="bootstrap" :max="max" show-progress animated variant="warning"></b-progress>
        </b-col>
        <b-col md="6" sm="12">
          <h3 class="mt-4 text-center html">
            HTML
            <i class="fab fa-html5"></i>
          </h3>
          <b-progress :value="html" :max="max" show-progress animated variant="secondary"></b-progress>
          <h3 class="mt-4 text-center css">
            CSS
            <i class="fab fa-css3-alt"></i>
          </h3>
          <b-progress :value="css" :max="max" show-progress animated variant="danger"></b-progress>
          <h3 class="mt-4 text-center mysql">MySQL</h3>
          <b-progress :value="mysql" :max="max" show-progress animated variant="warning"></b-progress>
          <h3 class="mt-4 text-center jquery">JQUERY</h3>
          <b-progress :value="jquery" :max="max" show-progress animated variant="dark"></b-progress>
          <h3 class="mt-4 text-center wordpress">WordPress</h3>
          <b-progress :value="wordpress" :max="max" show-progress animated></b-progress>
        </b-col>
      </b-row>
    </b-container>
    <b-container class="d-flex justify-content-around icon my-5">
      <i class="fas fa-desktop"></i>
      <i class="fas fa-tablet-alt"></i>
      <i class="fas fa-mobile-alt"></i>
    </b-container>
  </section>
</template>
<script>
import { setInterval } from "timers";

export default {
  props: {
    languageType: { type: String }
  },
  data() {
    return {
      php: 0,
      js: 0,
      symfony: 0,
      vjs: 0,
      html: 0,
      css: 0,
      mysql: 0,
      jquery: 0,
      bootstrap: 0,
      wordpress: 0,
      max: 100
    };
  },
  mounted() {
    this.progressBar();
  },
  methods: {
    progressBar() {
      setInterval(() => {
        this.php = 60;
        this.js = 60;
        this.symfony = 45;
        this.vjs = 45;
        this.html = 85;
        this.css = 85;
        this.mysql = 70;
        this.jquery = 70;
        this.bootstrap = 70;
        this.wordpress = 50;
      }, 1400);
    }
  }
};
</script>
<style scoped>
.code {
  color: rgb(209, 171, 179);
}
h3 {
  color: white;
  cursor: pointer;
}
.php:hover {
  color: black;
}
.js:hover {
  color: yellow;
}
.sf:hover {
  color: black;
}
.vjs:hover {
  color: #41b883;
}
.html:hover {
  color: #e44d26;
}
.css:hover {
  color: #264de4;
}
.mysql:hover {
  color: #e48e00;
}
.jquery:hover {
  color: #0769ad;
}
.bootstrap:hover {
  color: #8e59db;
}
.icon {
  font-size: 4rem;
}
</style>
