<template>
  <div id="app">
    <header class="menu-inicial bg-light">
      <b-navbar toggleable="md" variant="light" type="light" class="container">
        <b-navbar-brand :to="{name: 'home'}" exact class="mylogo">
          <h1>
            <span class="logostyle1">F</span>
            <span class="logostyle2">DeV</span>
          </h1>
        </b-navbar-brand>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav class="ml-auto nav-menu">
            <b-nav-item :to="{name:'presentation'}">
              <transition name="fade">
                <span v-if="lang === 'en'">about me</span>
                <span v-else-if="lang === 'fr'">presentation</span>
                <span v-else-if="lang=== 'pt'">sobre</span>
              </transition>
            </b-nav-item>
            <b-nav-item :to="{name:'competences'}">
              <span v-if="lang === 'en'">skills</span>
              <span v-else-if="lang === 'fr'">compétences</span>
              <span v-else-if="lang=== 'pt'">habilidades</span>
            </b-nav-item>
            <b-nav-item :to="{name:'portfolio'}">
              <span v-if="lang === 'en'">portfolio</span>
              <span v-else-if="lang === 'fr'">portfolio</span>
              <span v-else-if="lang=== 'pt'">portfólio</span>
            </b-nav-item>
            <b-nav-item :to="{name:'contact'}">
              <span v-if="lang === 'en'">contact</span>
              <span v-else-if="lang === 'fr'">contact</span>
              <span v-else-if="lang=== 'pt'">contato</span>
            </b-nav-item>
          </b-navbar-nav>

          <!-- Right aligned nav items -->
          <b-navbar-nav>
            <b-nav-item-dropdown text="Lang" right>
              <b-dropdown-item @click="language('en')">english</b-dropdown-item>
              <b-dropdown-item @click="language('fr')">french</b-dropdown-item>
              <b-dropdown-item @click="language('pt')">portuguese</b-dropdown-item>
            </b-nav-item-dropdown>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </header>
    <main>
      <b-container class="principal">
        <transition
          name="view"
          enter-active-class="animated bounceInLeft delay"
          leave-active-class="animated zoomOutRight"
        >
          <router-view :languageType="lang"></router-view>
        </transition>
      </b-container>
    </main>
  </div>
</template>

<script>
export default {
  name: "app",
  components: {},

  data() {
    return {
      lang: "fr"
    };
  },
  mounted() {
    const html = document.documentElement;
    html.setAttribute("lang", this.lang);
  },
  methods: {
    language(idioma) {
      const html = document.documentElement;

      if (idioma == "en") {
        this.lang = "en";
        html.setAttribute("lang", this.lang);
      }
      if (idioma == "fr") {
        this.lang = "fr";
        html.setAttribute("lang", this.lang);
      }
      if (idioma == "pt") {
        this.lang = "pt";
        html.setAttribute("lang", this.lang);
      }
    }
  }
};
</script>

