<template>
  <div @click.stop>
    <div class="lightbox__loading" v-if="loading">
      <b-spinner type="grow" style="width: 4rem; height: 4rem;" label="Spinning" variant="dark"></b-spinner>
    </div>
    <transition name="fade">
      <img :src="src" class="lightbox__image img-fluid" :style="style" :key="src">
    </transition>
  </div>
</template>
<script>
export default {
  props: {
    image: String
  },
  data() {
    return {
      loading: true,

      src: false,

      style: {}
    };
  },
  methods: {
    resizeImage(image) {
      let width = image.width;
      let height = image.height;
      if (width > window.innerWidth || height > window.innerHeight) {
        let ratio = width / height;
        let windowRatio = window.innerWidth / window.innerHeight;
        if (ratio > windowRatio) {
          width = window.innerWidth;
          height = width / ratio;
        } else {
          height = window.innerHeight;
          width = height * ratio;
        }
      }
      this.style = {
        width: width + "px",
        height: height + "px",
        top: (window.innerHeight - height) * 0.5 + "px",
        left: (window.innerWidth - width) * 0.5 + "px"
      };
    }
  },
  mounted() {
    let image = new window.Image();
    image.onload = _ => {
      this.loading = false;
      this.src = this.image;
      this.resizeImage(image);
    };
    image.src = this.image;
    this.resizeEvent = () => {
      this.resizeImage(image);
    };
    window.addEventListener("resize", this.resizeEevnt);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeEevnt);
  }
};
</script>
<style>
.lightbox__image {
  position: fixed;
}
.lightbox__loading {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  margin: 0 -20px 0 -20px;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
</style>

